import React from "react";
import Link from "gatsby-link";

import LAGraph from '../components/LAGraph';

const IndexPage = () => (
  <div className="container flex mb-4">
    <LAGraph className="w-2/3 h-12" />
    <div className="w-1/3 h-12 mt-8">
    <h2 className="relative pin-l pin-t bg-orange-lighter inline-block -mt-8 p-8 content-start">
      LA data Graph - Pre ICO by city, except from LA*
    </h2>
    <p className="bg-orange-base p-4">* LA had size 67 which would of dwarfed other cities</p>
    </div>
  </div>
);

export default IndexPage;



